.solarSystem {
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: 0 auto;
    width: 100%;
    height: 100vh;
    background-color: black;

}

.nameNone {
    opacity: 0;
}

.name {
    font-family: Moon-Light;
    opacity: 0;
    text-shadow: 2px 2px #000;
    letter-spacing: .2em;
    justify-content: center;
    align-items: center;
    position: absolute;
    color: white;
    z-index: 10;
    font-size: 500%;
    padding-top: 10px;
    animation: revealName ease-in 3s ;
    animation-fill-mode: forwards;
}

.name p {
    word-spacing: 25px;
}

@keyframes revealName {
    0% {
        opacity: 0;
        clip-path: inset(0px 50% 0px 50%);
        -webkit-clip-path: inset(0px 50% 0px 50%);
    }

    100% {
        opacity: 1;
        clip-path: inset(0px 0px 0px 0px);
        -webkit-clip-path: inset(0px 0px 0px 0px);
    }
}


.sun {
    width: 100px;
    height: 100px;
    border-radius: 100%;
    position: absolute;
    z-index: 10;
    background-image: url('./sun.jpg');
    background-size: cover;
    box-shadow: -5px -5px 25px 1px rgba(0, 0, 0, .5) inset, 0 0 5px 1px rgba(0, 0, 0, .9);
    animation: planetSpin 4000s linear infinite, SunFadeIn 2.5s ease-out;
}


.planet1Orbit {
    position: absolute;
    width: 150px;
    height: 150px;
    -webkit-transform: scaleX(3) skewY(-30deg);
    -moz-transform: scaleX(3) skewY(-30deg);
    -o-transform: scaleX(3) skewY(-30deg);
    -ms-transform: scaleX(3) skewY(-30deg);
    transform: scaleX(3) skewY(-30deg);
    border-style: solid;
    border-color: rgba(255, 255, 255, 0.2);
    border-width: 1px;
    border-radius: 50%;
    animation: planetFadeInTop 1.3s ease-out;
}

.planet1Rotate {
    position: absolute;
    top: 3%;
    width: 110%;
    height: 100%;
    -webkit-animation: circle 5s infinite linear;
    -moz-animation: circle 5s infinite linear;
    -o-animation: circle 5s infinite linear;
    animation: circle 5s infinite linear;

    -webkit-transform-origin: 50% 50%;
    -moz-transform-origin: 50% 50%;
    -o-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
}

.planet1CounterRotate {
    width: 50px;
    height: 50px;
    overflow: hidden;
    -webkit-animation: ccircle 5s infinite linear;
    -moz-animation: ccircle 5s infinite linear;
    -o-animation: ccircle 5s infinite linear;
    animation: ccircle 5s infinite linear;
}

.planet1 {
    width: 5vh;
    height: 5vh;
    background-color: green;
    background-image: url('./Mercury.jpeg');
    background-size: cover;
    box-shadow: -5px -5px 25px 1px rgba(0, 0, 0, .5) inset, 0 0 5px 1px rgba(0, 0, 0, .9);
    border-radius: 100%;
    display: block;
    -webkit-animation: sizeOfInner 5s infinite linear;
    -moz-animation: sizeOfInner 5s infinite linear;
    -o-animation: sizeOfInner 5s infinite linear;
    animation: sizeOfInner 5s infinite linear;
}


.planet2Orbit {
    position: absolute;
    width: 200px;
    height: 200px;
    -webkit-transform: scaleX(3) skewY(-30deg);
    -moz-transform: scaleX(3) skewY(-30deg);
    -o-transform: scaleX(3) skewY(-30deg);
    -ms-transform: scaleX(3) skewY(-30deg);
    transform: scaleX(3) skewY(-30deg);
    border-style: solid;
    border-color: rgba(255, 255, 255, 0.2);
    border-width: 1px;
    border-radius: 50%;
    animation: planetFadeInBottom 2s ease-out;
}

.planet2Rotate {
    position: absolute;
    overflow: hidden;
    width: 90%;
    height: 100%;
    left: 5%;
    -webkit-animation: circle 10s infinite linear;
    -moz-animation: circle 10s infinite linear;
    -o-animation: circle 10s infinite linear;
    animation: circle 10s infinite linear;

    -webkit-transform-origin: 50% 50%;
    -moz-transform-origin: 50% 50%;
    -o-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
}

.planet2CounterRotate {
    overflow: hidden;
    width: 50px;
    height: 50px;
    -webkit-animation: ccircle 10s infinite linear;
    -moz-animation: ccircle 10s infinite linear;
    -o-animation: ccircle 10s infinite linear;
    animation: ccircle 10s infinite linear;
}

.planet2 {
    width: 50px;
    height: 50px;
    left: 0px;
    top: 0px;
    background-image: url('./mars.png');
    background-size: cover;
    box-shadow: -5px -5px 25px 1px rgba(0, 0, 0, .5) inset, 0 0 5px 1px rgba(0, 0, 0, .9);
    border-radius: 100%;
    display: block;
    -webkit-animation: sizeOfInner 10s infinite linear;
    -moz-animation: sizeOfInner 10s infinite linear;
    -o-animation: sizeOfInner 10s infinite linear;
    animation: sizeOfInner 10s infinite linear, planetSpin 400s linear infinite;
}


.planet3Orbit {
    position: absolute;
    width: 250px;
    height: 250px;
    -webkit-transform: scaleX(3) skewY(-30deg);
    -moz-transform: scaleX(3) skewY(-30deg);
    -o-transform: scaleX(3) skewY(-30deg);
    -ms-transform: scaleX(3) skewY(-30deg);
    transform: scaleX(3) skewY(-30deg);
    border-style: solid;
    border-color: rgba(255, 255, 255, 0.2);
    border-width: 1px;
    border-radius: 50%;
    animation: planetFadeInTop 1.7s ease-out;
}

.planet3Rotate {
    position: absolute;
    overflow: hidden;
    width: 80%;
    height: 100%;
    left: 10%;
    -webkit-animation: circle 15s infinite linear;
    -moz-animation: circle 15s infinite linear;
    -o-animation: circle 15s infinite linear;
    animation: circle 15s infinite linear;

    -webkit-transform-origin: 50% 50%;
    -moz-transform-origin: 50% 50%;
    -o-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
}

.planet3CounterRotate {
    overflow: hidden;
    width: 50px;
    height: 50px;
    -webkit-animation: ccircle 15s infinite linear;
    -moz-animation: ccircle 15s infinite linear;
    -o-animation: ccircle 15s infinite linear;
    animation: ccircle 15s infinite linear;
}

.planet3 {
    width: 50px;
    height: 50px;
    left: 0px;
    top: 0px;
    background-image: url('./Jupiter.jpg');
    background-size: cover;
    box-shadow: -5px -5px 25px 1px rgba(0, 0, 0, .5) inset, 0 0 5px 1px rgba(0, 0, 0, .9);
    border-radius: 100%;
    display: block;
    -webkit-animation: sizeOfInner 15s infinite linear;
    -moz-animation: sizeOfInner 15s infinite linear;
    -o-animation: sizeOfInner 15s infinite linear;
    animation: sizeOfInner 15s infinite linear, planetSpin 500s linear infinite;
}

@keyframes planetSpin {
    from {
        background-position: 0;
    }

    to {
        background-position: -10000px;
    }
}

@keyframes SunFadeIn {
    from {
        opacity: 0;
        transform: translateY(-200px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}


@keyframes planetFadeInTop {
    from {
        opacity: 0;
        transform: translateY(-130px) scaleX(3) skewY(-30deg);
    }

    to {
        opacity: 1;
        transform: translateY(0) scaleX(3) skewY(-30deg);
    }
}

@keyframes planetFadeInBottom {
    from {
        opacity: 0;
        transform: translateY(130px) scaleX(3) skewY(-30deg);
    }

    to {
        opacity: 1;
        transform: translateY(0) scaleX(3) skewY(-30deg);
    }
}




.stars,
.twinkling {
    position: absolute;
    display: block;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100vh;
}

.stars {
    z-index: 0;
    background: rgb(0, 0, 0) url('./Stars-WillHord.jpg') repeat top center;
    background-size: cover;
}

.twinkling {
    z-index: 1;
    background: transparent url('background2.png') repeat top center;
    animation: move-twink-back 50s linear infinite;
}

@keyframes move-twink-back {
    from {
        background-position: 0 0;
    }

    to {
        background-position: -10000px 5000px;
    }
}




@keyframes sizeOfInner {
    0% {
        transform: scaleX(0.33) scale(.6);
    }

    20% {
        transform: scaleX(0.33) scale(.6);
    }

    40% {
        transform: scaleX(0.33) scale(.75);
    }

    60% {
        transform: scaleX(0.33) scale(1);
    }

    80% {
        transform: scaleX(0.33) scale(.75);
    }

    100% {
        transform: scaleX(0.33) scale(.6);
    }
}


@-webkit-keyframes circle {
    from {
        -webkit-transform: rotateZ(0deg)
    }

    to {
        -webkit-transform: rotateZ(360deg)
    }
}

@-moz-keyframes circle {
    from {
        -moz-transform: rotateZ(0deg)
    }

    to {
        -moz-transform: rotateZ(360deg)
    }
}

@-o-keyframes circle {
    from {
        -o-transform: rotateZ(0deg)
    }

    to {
        -o-transform: rotateZ(360deg)
    }
}

@keyframes circle {
    from {
        transform: rotateZ(0deg)
    }

    to {
        transform: rotateZ(360deg)
    }
}


@-webkit-keyframes ccircle {
    from {
        -webkit-transform: rotateZ(360deg)
    }

    to {
        -webkit-transform: rotateZ(0deg)
    }
}

@-moz-keyframes ccircle {
    from {
        -moz-transform: rotateZ(360deg)
    }

    to {
        -moz-transform: rotateZ(0deg)
    }
}

@-o-keyframes ccircle {
    from {
        -o-transform: rotateZ(360deg)
    }

    to {
        -o-transform: rotateZ(0deg)
    }
}

@keyframes ccircle {
    from {
        transform: rotateZ(360deg)
    }

    to {
        transform: rotateZ(0deg)
    }
}