.top-menu-item:hover {
    opacity: 1;
}
.top-menu-lead {
    padding: 10px;
    display: inline-block;
    float: left;
    font-size: 25px;
    padding-left: 7%;
    font-family: Moon;
    letter-spacing: .1em;
}

@media screen and (max-width: 705px) {
    .top-menu-lead {
        position: absolute;
        top: 10px;
        left: 10px;
        float: none;
    }
}
.burgerIconContainer {
    position: absolute;
    top: 10px;
    right: 1rem;
    flex-direction: column;
    justify-content: space-around;
    width: 2rem;
    height: 2rem;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    z-index: 10;
    display: none;
}


.hamburgerBar {
    width: 2rem;
    height: 0.25rem;
    border-radius: 10px;
    transition: all 0.3s linear;
    position: relative;
    transform-origin: 1px;
    background-color: white;
}


@media screen and (max-width: 705px) {
    .burgerIconContainer {
        display: flex;
        padding: 0;
    }
}
/* TODO: Add fade in animation for top menu */
.top-menu {
    overflow: hidden;
    padding-top: .5%;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    background: transparent;
    position: absolute;
    width: 100%;
    z-index: 10;
    color: red;
    font-family: 'Montserrat', sans-serif;
}

.top-menu>.right {
    float: right;
    padding-right: 10%;
}

.top-menu-item {
    font-size: 17px;
    transition: .2s;
}

.top-menu>.right:hover>.top-menu-item {
    opacity: 0.2;
    transition: 0.2s;
}

.top-menu>.right>.top-menu-item:hover {
    opacity: 1;
}


.top-menu>.right>* {
    margin-left: 5px;
}

.top-menu>.right>* {
    padding: 11.4px;
    display: inline-block;
    text-align: center;
    min-width: 50px;
}

.top-menu *:hover {
    cursor: pointer;
}

.clear-fix {
    clear: both;
}

.top-menu .top-menu-icon {
    padding: 10px;
    position: absolute;
    top: 0;
    right: 0;
    display: none;
    z-index: 1000;
}

@media screen and (max-width: 705px) {
    .top-menu {
        padding: 40px 20px 20px 20px;
        max-height: 0;
        overflow: hidden;
    }

    .top-menu>.right {
        display: none;
    }

    .top-menu-icon {
        display: block !important;
    }
}

@media screen and (max-width: 705px) {
    .top-menu.toggled {
        padding: 0 0 0 0;
        max-height: 1500px;
        transition: max-height 1s;
        background-color: #282E34;
        z-index: 100;
    }

    .top-menu.toggled *:not(.top-menu-lead):not(.burgerIconContainer) {
        float: none;
        display: block !important;
        text-align: left;
        margin: 0;
    }
}
.solarSystem {
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: 0 auto;
    width: 100%;
    height: 100vh;
    background-color: black;

}

.nameNone {
    opacity: 0;
}

.name {
    font-family: Moon-Light;
    opacity: 0;
    text-shadow: 2px 2px #000;
    letter-spacing: .2em;
    justify-content: center;
    align-items: center;
    position: absolute;
    color: white;
    z-index: 10;
    font-size: 500%;
    padding-top: 10px;
    animation: revealName ease-in 3s ;
    animation-fill-mode: forwards;
}

.name p {
    word-spacing: 25px;
}

@keyframes revealName {
    0% {
        opacity: 0;
        clip-path: inset(0px 50% 0px 50%);
        -webkit-clip-path: inset(0px 50% 0px 50%);
    }

    100% {
        opacity: 1;
        clip-path: inset(0px 0px 0px 0px);
        -webkit-clip-path: inset(0px 0px 0px 0px);
    }
}


.sun {
    width: 100px;
    height: 100px;
    border-radius: 100%;
    position: absolute;
    z-index: 10;
    background-image: url(/static/media/sun.47e73fae.jpg);
    background-size: cover;
    box-shadow: -5px -5px 25px 1px rgba(0, 0, 0, .5) inset, 0 0 5px 1px rgba(0, 0, 0, .9);
    animation: planetSpin 4000s linear infinite, SunFadeIn 2.5s ease-out;
}


.planet1Orbit {
    position: absolute;
    width: 150px;
    height: 150px;
    transform: scaleX(3) skewY(-30deg);
    border-style: solid;
    border-color: rgba(255, 255, 255, 0.2);
    border-width: 1px;
    border-radius: 50%;
    animation: planetFadeInTop 1.3s ease-out;
}

.planet1Rotate {
    position: absolute;
    top: 3%;
    width: 110%;
    height: 100%;
    animation: circle 5s infinite linear;
    transform-origin: 50% 50%;
}

.planet1CounterRotate {
    width: 50px;
    height: 50px;
    overflow: hidden;
    animation: ccircle 5s infinite linear;
}

.planet1 {
    width: 5vh;
    height: 5vh;
    background-color: green;
    background-image: url(data:image/jpeg;base64,);
    background-size: cover;
    box-shadow: -5px -5px 25px 1px rgba(0, 0, 0, .5) inset, 0 0 5px 1px rgba(0, 0, 0, .9);
    border-radius: 100%;
    display: block;
    animation: sizeOfInner 5s infinite linear;
}


.planet2Orbit {
    position: absolute;
    width: 200px;
    height: 200px;
    transform: scaleX(3) skewY(-30deg);
    border-style: solid;
    border-color: rgba(255, 255, 255, 0.2);
    border-width: 1px;
    border-radius: 50%;
    animation: planetFadeInBottom 2s ease-out;
}

.planet2Rotate {
    position: absolute;
    overflow: hidden;
    width: 90%;
    height: 100%;
    left: 5%;
    animation: circle 10s infinite linear;
    transform-origin: 50% 50%;
}

.planet2CounterRotate {
    overflow: hidden;
    width: 50px;
    height: 50px;
    animation: ccircle 10s infinite linear;
}

.planet2 {
    width: 50px;
    height: 50px;
    left: 0px;
    top: 0px;
    background-image: url(/static/media/mars.cf70777e.png);
    background-size: cover;
    box-shadow: -5px -5px 25px 1px rgba(0, 0, 0, .5) inset, 0 0 5px 1px rgba(0, 0, 0, .9);
    border-radius: 100%;
    display: block;
    animation: sizeOfInner 10s infinite linear, planetSpin 400s linear infinite;
}


.planet3Orbit {
    position: absolute;
    width: 250px;
    height: 250px;
    transform: scaleX(3) skewY(-30deg);
    border-style: solid;
    border-color: rgba(255, 255, 255, 0.2);
    border-width: 1px;
    border-radius: 50%;
    animation: planetFadeInTop 1.7s ease-out;
}

.planet3Rotate {
    position: absolute;
    overflow: hidden;
    width: 80%;
    height: 100%;
    left: 10%;
    animation: circle 15s infinite linear;
    transform-origin: 50% 50%;
}

.planet3CounterRotate {
    overflow: hidden;
    width: 50px;
    height: 50px;
    animation: ccircle 15s infinite linear;
}

.planet3 {
    width: 50px;
    height: 50px;
    left: 0px;
    top: 0px;
    background-image: url(/static/media/Jupiter.12c20436.jpg);
    background-size: cover;
    box-shadow: -5px -5px 25px 1px rgba(0, 0, 0, .5) inset, 0 0 5px 1px rgba(0, 0, 0, .9);
    border-radius: 100%;
    display: block;
    animation: sizeOfInner 15s infinite linear, planetSpin 500s linear infinite;
}

@keyframes planetSpin {
    from {
        background-position: 0;
    }

    to {
        background-position: -10000px;
    }
}

@keyframes SunFadeIn {
    from {
        opacity: 0;
        transform: translateY(-200px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}


@keyframes planetFadeInTop {
    from {
        opacity: 0;
        transform: translateY(-130px) scaleX(3) skewY(-30deg);
    }

    to {
        opacity: 1;
        transform: translateY(0) scaleX(3) skewY(-30deg);
    }
}

@keyframes planetFadeInBottom {
    from {
        opacity: 0;
        transform: translateY(130px) scaleX(3) skewY(-30deg);
    }

    to {
        opacity: 1;
        transform: translateY(0) scaleX(3) skewY(-30deg);
    }
}




.stars,
.twinkling {
    position: absolute;
    display: block;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100vh;
}

.stars {
    z-index: 0;
    background: rgb(0, 0, 0) url(/static/media/Stars-WillHord.6702e09c.jpg) repeat top center;
    background-size: cover;
}

.twinkling {
    z-index: 1;
    background: transparent url(/static/media/background2.3e32a839.png) repeat top center;
    animation: move-twink-back 50s linear infinite;
}

@keyframes move-twink-back {
    from {
        background-position: 0 0;
    }

    to {
        background-position: -10000px 5000px;
    }
}




@keyframes sizeOfInner {
    0% {
        transform: scaleX(0.33) scale(.6);
    }

    20% {
        transform: scaleX(0.33) scale(.6);
    }

    40% {
        transform: scaleX(0.33) scale(.75);
    }

    60% {
        transform: scaleX(0.33) scale(1);
    }

    80% {
        transform: scaleX(0.33) scale(.75);
    }

    100% {
        transform: scaleX(0.33) scale(.6);
    }
}

@keyframes circle {
    from {
        transform: rotateZ(0deg)
    }

    to {
        transform: rotateZ(360deg)
    }
}

@keyframes ccircle {
    from {
        transform: rotateZ(360deg)
    }

    to {
        transform: rotateZ(0deg)
    }
}
